<template>
  <div>
  <headerPage></headerPage>
<div class="overallDivs"> 

<row class="rowone ml-0 mr-0">
</row>
<row class="rowtwo ml-0 mr-0">
  <card class="cardSigup text-center">
    <img src="../assets/justopslogo.svg" class="logoImgs">
    <p class="font-weight-bold" style="font-size: 20px;">Sign Up for free</p>
    <p style="font-size: 16px;">14 days of free trial with all premium features unlocked</p>
  <button type="button" v-on:click="googlesignup" id="googleSignUpp" class="googleButtons btn btn-google-plus"><span class=""><i class="fa fa-google-plus pr-1 mr-2"></i></span>  Sign Up With Google </button>
   <p>By signing up, you are agreeing to our <br/></p>
   <p><a href="#"><span class="d-inline-block termsHov" style="color: rgb(139, 195, 74); font-weight: bold;">Terms of Service </span></a><span> and </span><a><span class="d-inline-block privHov" style="color: rgb(139, 195, 74); font-weight: bold;">Privacy Policy</span></a></p>
  <p>Already have an account ?<a href="#/signin"><span class="ml-2 signinHov  d-inline-block" style="color: rgb(139, 195, 74); font-weight: bold;"> Sign In</span></a></p>
  <Spinner class="spinners" v-show="isShow" name="ball-clip-rotate-multiple" color="#000000"/>

</card>
</row> 
</div>
</div>
</template>
<script>
import Btn from '../components/Button';
import Column from '../components/Col';
import Row from '../components/Row';
import Container from '../components/Container';
import Card from '../components/Card';
import CardBody from '../components/CardBody';
import firebase from 'firebase';
import axios from 'axios';
import Vue from 'vue';
import VueSession from 'vue-session';
import headerPage from '../docs/headerPage.vue';
import {mapState} from 'vuex';
Vue.use(VueSession);
import Notify from 'vue2-notify';
Vue.use(Notify);
import Spinner from 'vue-spinkit';
Vue.component('Spinner', Spinner);

export default {
  name: 'JustDeploy',
  computed: mapState([
    'userEngineURL',
    'engineURL',
    'jobEngineURL'
  ]),
  components: {
    Btn,
    Column,
    Row,
    Container,
    Card,
    CardBody,
    headerPage,
  },
  data(){
    return {
      username: '',
      email: '',
      phonenumber: '',
      photo: '',
      uid: '',
      text: '',
      isShow: 0,       
    };
  },
  mounted() {
    // console.log(localStorage.getItem('sonarUrl'));
    if (localStorage.getItem('sonarUrl') == 0){
      localStorage.setItem('sonarUrl', 2);
      location.reload(true);
    }
  },
  methods: {
    errorNotification(){
      this.$notify.error('Please Try Again Later!!!');
    },
    googlesignup() {
      var baseProvider = new firebase.auth.GoogleAuthProvider();
      firebase.auth().signInWithPopup(baseProvider)
        .then(function(result){
          this.isShow = 1;
          // console.log(result);
          // console.log(result.user.email);
          var fields = result.user.displayName.split(' ');
          let newUser = {
            "firstName": fields[0],
            "lastName": fields[1],
            "email": result.user.email, 
            "profileImage": result.user.photoURL,
            "firebaseId": result.user.uid, 
          };
          // console.log(newUser);
          this.signup(newUser);
        }.bind(this))
        .catch(function(err){
          if (err){
            this.isShow = 0;
            this.errorNotification();
          }
        });
    },
    signup(newUser) {
      axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
      axios.post(this.userEngineURL+"google_login", newUser)
        .then(function(response){
          this.isShow = 1;
          if (response.status === 200) {
            // console.log(response.data);
            var res = response.data;

            localStorage.setItem('firstName', res.data.firstName);
            localStorage.setItem('lastname', res.data.lastname);
            localStorage.setItem('email', res.data.email);
            localStorage.setItem('userId', res.data._id);
            localStorage.setItem('profileImage', res.data.profileImage);
            localStorage.setItem('loggedIn', '1');
            // console.log(this.$session.get('userName'));
            // console.log(this.$session.get('lastname'));
            // console.log(this.$session.get('email'));
            //this.$session.get('userId',res.data._id);
            // this.$swal('User', 'Successfully LoggedIn');
            if (response.data.message == "Logged In Successfully") {
              this.$notify.success('LoggedIn Successfully!!');  
            }
            if (response.data.message == "User created") {
              this.$notify.success('SignedUp Successfully!!');  
            }
            this.$router.push('/dashboard');
          }
          else {
            this.isShow = 0;
            this.$router.push('/');
          }
        }.bind(this))
        .catch(function(err){
          if (err){
            this.isShow = 0;
            this.errorNotification();
          }
        }.bind(this));
    },
  }
};
</script>
<style scoped>
.spinners{
  margin: 1em 0em 0em 16em !important;
  width: 10em;
  height: 5em auto ;
}
.gplus{
  border-right: 1px solid #ffffff !important;
}
.rowtwo{
  height: 37em !important;
  background-image: url("../assets/Just Deploy_Sign in background.svg");
}
.rowone{
  height: 16em !important;
}
.overallDivs{
    background-color: #ff5722ff !important;
}
.cardSigup{
    width: 38%!important;
    margin: -14em auto 0em auto !important;
    height: 535px !important;
    border-radius: 4px !important;
    z-index: 10 !important;
  }
  .logoImgs{
    margin: 4em auto 2em auto !important;
    width: 12% !important;
  }
  .googleButtons{
    margin: 2em auto 3em auto !important;
    background: #ca3523 !important;
    border-radius: 3px !important;
    text-transform: none !important;
    width: 55% !important;
    height: 9% !important;
    font-size: 17px !important;
  }
  .signinHov::after {
    content: '';
    display: block;
    width: 0px;
    height: 2px;
    background: #8bc34aff;
    transition: width .3s;
  }
   .signinHov:hover::after {
    width: 100%;
  }
  .termsHov::after {
    content: '';
    display: block;
    width: 0px;
    height: 2px;
    background: #8bc34aff;
    transition: width .3s;
  }
  .termsHov:hover::after {
    width: 100%;
  }
  .privHov::after {
    content: '';
    display: block;
    width: 0px;
    height: 2px;
    background: #8bc34aff;
    transition: width .3s;
  }
  .privHov:hover::after{
    width: 100%;
  }
 @media only screen and (min-width: 340px) and (max-width: 639.98px)
 {
 .cardSigup{
   width: 100% !important;
   height: auto !important;
   padding: 2em !important;
   box-shadow: none !important;

 }
 .googleButtons{
    font-size: 14px !important;
    width: 80% !important;
 }
 .logoImgs{
  margin: 4em auto 2em auto !important;
    width: 60% !important;
 }
.overallDivs{
   background-color: #ffffff !important;
}
.rowone{
  height: 11em !important;
}
}
@media only screen and (min-width: 300px) and (max-width: 339.98px)
 {
 .cardSigup{
   width: 100% !important;
   height: auto !important;
   padding: 2em !important;
   box-shadow: none !important;

 }
 .googleButtons{
    font-size: 14px !important;
    width: 100% !important;
 }
 .logoImgs{
  margin: 4em auto 2em auto !important;
    width: 60% !important;
 }
.overallDivs{
   background-color: #ffffff !important;
}
.rowone{
  height: 11em !important;
}
 }
@media only screen and (min-width: 640px) and (max-width: 800px){
 .cardSigup {
    width: 70% !important;
   padding: 2em !important;
   margin: -12em auto 0em auto !important;
 }
 .googleButtons{
    font-size: 14px !important;
    width: 70% !important;
 }
 .rowtwo{
    height: 45em !important;
}
.overallDivs{
margin-top: -0.1em !important;
}
.spinners {
  margin: 1em 0em 0em 15em !important;
}
}
/*@media (min-width: 768px) and (max-width: 991px) {
.rowtwo{
  height: 49.5em !important;
}
}*/
@media (min-width: 992px) and (max-width: 1199px)
 {
 .rowtwo{
    height: 44em !important;
    }
  .overallDivs {
    margin-top: -0.1em !important;
  }
  .cardSigup {
    margin: -13em auto 0em auto !important;
    width: 50%!important;
 }
 .rowone{
  height: 22em !important;
}
.googleButtons{
  width: 60% !important;
}
.spinners {
  margin: 1em 0em 0em 16em !important;
}
}
</style>


