<template>
  <div>
  <headerPage :key="$route.fullPath"></headerPage>
<div class="container-fluid p-5 contFluied">

  <card class="overallCard p-2">
  <row>

    <column md="3" sm="12">
    <p class="mt-3 ml-3"><span class="font-weight-bold">{{ jobName }} : </span><span class="pendingStatus font-weight-bold" v-if = 'buildStatus == ""'>INPROGRESS</span>
      <span class="pendingStatus font-weight-bold" v-if = 'buildStatus == "IN_PROGRESS"'>INPROGRESS</span>
    <span class="succesStatus font-weight-bold" v-if = 'buildStatus == "SUCCESS"'>{{buildStatus}}</span>
    <span class="failedStatus font-weight-bold" v-if = 'buildStatus == "FAILED"'>{{buildStatus}}</span>
    </p>
    </column>
    <column md="4" sm="12">
      <p class="mt-3 ml-3">Log time: {{ this.millis }}<!-- <br/><span>Finished: </span> --></p>
    </column>
    <column md="5" sm="12">
      <p class="mt-3 ml-3">No of Deploys: {{noOfDeploy}}<br/><span>Deployed by: {{userName}}</span></p>
    </column>
    </row>
  </card>
  <br/>
       <div class="w-100 text-center " style="height: 15px; border-bottom: 3px solid #0000007d;">
       <span class="font-weight-bold" style="font-size: 20px; background-color: #FFFFFF; padding: 0 10px;">BUILD LOG</span>
          </div><br/>
     <card class="cardinprogress p-2 mb-3 mt-3" v-if="buildInprogress == 0">
      <row>
        <column md="3" sm="12">    
          <p class="mt-3 ml-3 font-weight-bold" ><span class="font-weight-bold">Build Initialized</span>
            <span><img src="../assets/loading.gif" class="buildLoader"/></span>
          </p>
        </column>
        </row>
      </card>
      <card class="cardStarted p-2 mb-3 mt-3" v-else>
      <row>
        <column md="3" sm="12">    
          <p class="mt-3 ml-3 font-weight-bold"><span class="font-weight-bold">Build Initiated</span>
          </p>
        </column>
        </row>
      </card>
  <row>

    <column class="mb-3" md="12" sm="12" v-for="stage in stages" :key="stage.id">
      <card class="cardinprogress" v-if="stage.status === 'IN_PROGRESS'">
       <span class="ml-3 p-3 mt-3 mb-3 font-weight-bold">{{stage.name}} : In progress <img src="../assets/loader2.gif" class="loaderGif"></span>
      </card>
    <!-- {{(this.millis / 1000).toFixed(2)}} -->
      <card class="cardStarted" v-if="stage.status === 'SUCCESS'">
          <p class="mt-3 p-3 ml-3 font-weight-bold" v-if="stage.durationMillis < 1000">{{stage.name}} :  <span class=" font-weight-bold" >SUCCESS</span><span class="mr-3" style="float: right;  font-style: italic;">00:00:{{stage.durationMillis.toFixed(0)}} ms</span></p>
          <p class="mt-3 p-3 ml-3 font-weight-bold" v-if="stage.durationMillis >= 1000 && stage.durationMillis < 60000">{{stage.name}} :  <span class=" font-weight-bold" >SUCCESS</span><span class="mr-3" style="float: right;  font-style: italic;">00:{{(stage.durationMillis/1000).toFixed(0)}} sec</span></p>
          <p class="mt-3 p-3 ml-3 font-weight-bold" v-if="stage.durationMillis >= 60000">{{stage.name}} :  <span class=" font-weight-bold" >SUCCESS</span><span class="mr-3" style="float: right;  font-style: italic;">{{(stage.durationMillis/60000).toFixed(0)}} min</span></p>
          <!-- <p class="mt-3 p-3 ml-3 font-weight-bold" v-if="stage.durationMillis > 6000 || stage.durationMillis >= 1000">{{stage.name}} :  <span class=" font-weight-bold" >Completed</span><span class="mr-3" style="float: right;  font-style: italic;">00:{{stage.durationMillis}} Seconds</span></p> -->
          <!-- <p class="mt-3 p-3 ml-3 font-weight-bold" >{{stage.name}} :  <span class=" font-weight-bold" >Completed</span></p>    -->    
      </card>     
      <card class="cardFailed" v-if="stage.status === 'FAILED'">
       <p class="mt-3 p-3 ml-3 font-weight-bold" v-if="stage.durationMillis < 1000">{{stage.name}} :  <span class=" font-weight-bold" >FAILED</span><span class="mr-3" style="float: right;  font-style: italic;">00:00:{{stage.durationMillis.toFixed(0)}} ms</span></p>
        <p class="mt-3 p-3 ml-3 font-weight-bold" v-if="stage.durationMillis >= 1000 && stage.durationMillis < 60000">{{stage.name}} :  <span class=" font-weight-bold" >FAILED</span><span class="mr-3" style="float: right;  font-style: italic;">00:{{(stage.durationMillis/1000).toFixed(0)}} sec</span></p>
        <p class="mt-3 p-3 ml-3 font-weight-bold" v-if="stage.durationMillis >= 60000">{{stage.name}} :  <span class=" font-weight-bold" >FAILED</span><span class="mr-3" style="float: right;  font-style: italic;">{{(stage.durationMillis/60000).toFixed(0)}} min</span></p>
        <!-- <p class="mt-3 p-3 ml-3 font-weight-bold" >{{stage.name}} :  <span class=" font-weight-bold" >Completed</span></p> -->       
      </card>  
    </column>

    <column md="12" sm="12" v-show="streamDisplay">
    <!-- v-html="streamData" -->
      <card class="buildRunstatus">
        <pre id= "streaming" v-html="streamData">
          <!-- {{streamData}} -->
        </pre>
        <!-- <img src="../assets/loader2.gif" class="loaderGif"> -->
      </card>
<!--       <card class="buildRunstatus"  v-else>
        <object :data="dataURL" class="objectHeight"></object>
      </card> -->
    </column>

    <column class="mt-3" md="12" sm="12">
      <card class="cardinprogress">
        <p class="mt-3 p-3 ml-3 font-weight-bold" v-if = 'buildStatus == "IN_PROGRESS"'>Build Status:  <span class="pendingStatus font-weight-bold">IN PROGRESS</span></p>
    </card>
    <card class="cardStarted">
        <p class="mt-3 p-3 ml-3 font-weight-bold"  v-if = 'buildStatus == "SUCCESS"' >Build Status:  <span class="succesStatus font-weight-bold">{{buildStatus}}</span></p></card>
        <card class="cardFailed">
        <p class="mt-3 p-3 ml-3 font-weight-bold" v-if = 'buildStatus == "FAILED"'>Build Status:  <span class="failedStatus font-weight-bold">{{buildStatus}}</span></p>
      </card>
      <!-- <img src="..assets/loading-gif.gif"> -->
    </column>

    <!--For future use cards(Don't remove this)-->

    <!-- <column class="p-3" md="12" sm="12">
      <card class="cards ">
        <p class="mt-3 p-3 font-weight-bold">Start Container<span class="float-right mr-3">00.45</span></p>
      </card>
    </column>
    <column class="p-3" md="12" sm="12">
      <card class="cards ">
        <p class="mt-3 p-3 font-weight-bold">Start Container<span class="float-right mr-3">00.43</span></p>
      </card>
    </column>
    <column class="p-3" md="12" sm="12">
      <card class="cardsFailed">
        <p class="mt-3 p-3 font-weight-bold">Start Container<span class="float-right mr-3">00.42</span></p>
      </card>
    </column>  -->
    <!-- <a href="#" v-on:click="response">select me</a> -->


  </row>
  </div>
  <div class="w-100 text-center mb-5">  
  <button type="button" id="getstartedHBtn" class="btn btn-success w-auto m-auto" v-on:click="activity()" :disabled="validated == 1">View Activity</button>
  </div>
<!--    <div id="loader-wrapper" v-show="showSpinner">
      <div class="loader-section">
     <img src="../assets/loader5.gif" class="spinnerLoader">
     </div>
     </div> -->
  </div>
</template>
<script>
import Container from '../components/Container.vue';
import Row from '../components/Row.vue';
import Column from '../components/Col.vue';
import Card from '../components/Card.vue';
import Btn from '../components/Button.vue';
import MdInput from '../components/MdInput.vue';
import MdTextarea from '../components/MdTextarea.vue';
import headerPage from '../docs/headerPage.vue';
import axios from 'axios';
import {mapState} from 'vuex';

// import axios from 'axios';
// import Vue from 'vue';

export default {
  name: 'buildLogPage',
  computed: mapState([
    'baseURL',
    'engineURL'
  ]),
  components: {
    Container,
    Row,
    Column,
    Card,
    Btn,
    'mdinput': MdInput,
    'mdtextarea': MdTextarea,
    headerPage,
  },
  data() {
    return { 
      info: "",
      buildId: '',
      statusNum : 0,
      userName : '',
      userId : '',
      jobName : '',
      buildNumber : '',
      streamURL: '',
      buildStatus: '',
      buildInprogress: 0,
      status:'',
      noOfDeploy : '',
      interval : '',
      startTimeMillis: '',
      durationMillis : '',
      stages : [],
      processTime : '',
      validated: 1,
      streamDisplay: 0,
      // showSpinner : 0,
      streamData: 'In progress.....'
    };
  },
  mounted() {
    // console.log("hi this is local storage from home :"+localStorage.getItem('sonarUrl'));
    if (localStorage.getItem('sonarUrl') == 0){
      localStorage.setItem('sonarUrl', 2);
      location.reload(true);
      // console.log("hi this is local storage from home after if :"+localStorage.getItem('sonarUrl'));
    }
    // console.log(this.baseURL);
    // console.log(this.engineURL);
    if (localStorage.getItem('loggedIn') == 1){
     // this.showSpinner = 1;
      //this.build();
      this.jobName = localStorage.getItem('currentJob');
      this.buildNumber = localStorage.getItem('buildNumber');
      this.userName = localStorage.getItem('firstName');
      this.userId = localStorage.getItem('userId');
      this.jobId = localStorage.getItem('jobId');
      this.noOfDeploy = localStorage.getItem('numOfDep');
      this.interval=setInterval(this.build, 2000);

    }  
    else {
      // this.$router.push('/signin');

    }  
  },
  methods: {
    notifyMsg() {
      this.$notify.error('Please Try Again!!');
    },
    timings(milliSecs) {
      // var current = Date.now();
      // var past = secs;
      var seconds = (milliSecs)/1000;
      seconds = Math.round(seconds);
      var d = Math.round(seconds);
      // console.log("ms: "+element.lastUpdated+" sec: "+seconds);
      if (seconds < 60){
        this.millis = seconds+" Seconds ago";
      }
      else if (seconds < 3600){
        var m = Math.floor(d % 3600 / 60);
        var mDisplay = m > 0 ? m + (m == 1 ? " minute ago " : " minutes ago") : "";
        this.millis = mDisplay;
      }
      else if (seconds < 86400){
        var h = Math.floor(d / 3600);
        // console.log(h);
        if (h < 24 && h > 0){
          var hDisplay = h > 0 ? h + (h == 1 ? " hour ago" : " hours ago ") : "";
          this.millis = hDisplay;
        }
        else if (h >= 24){
          var DateDisplay1 = h > 0 ? h + (h == 1 ? " Day ago, " : " Days ago") : "";
          this.millis = DateDisplay1;
        }
      }
      else {
        var h1 = Math.floor(d / 86400);
        var DateDisplay = h1 > 0 ? h1 + (h1 == 1 ? " Day ago, " : " Days ago") : "";
        this.millis = DateDisplay;
      }
    },
    activity() {
      var activityJobName=localStorage.getItem('currentJob');
      var activityJobId=localStorage.getItem('buildNumber');
      var buildHisId= localStorage.getItem('buildHisId');
      let activityData = {
        jobName : activityJobName,
        buildNumber : activityJobId,
        buildHistoryId : buildHisId
      };
      console.log(activityData);
      axios.post("https://merida-engine.herokuapp.com/job/activityInfo", activityData)
      .then(response => {
        console.log(response.data.gradePercentage);
        console.log(response.data.gradeValue);
        console.log(response.data.data.endTimeMillis);
        console.log(response.data.data.durationMillis);
        localStorage.setItem('activityPercentage', response.data.gradePercentage);
        console.log(localStorage.getItem('activityPercentage'));
        localStorage.setItem('activityValue', response.data.gradeValue);
        localStorage.setItem('timetaken', response.data.data.durationMillis);
        // console.log(localStorage.getItem('timetaken'));
        localStorage.setItem('lastupdate', response.data.data.endTimeMillis);
        // console.log(localStorage.getItem('lastupdate'));
        localStorage.setItem('unitTest', response.data.data.stages[4].status);
        localStorage.setItem('codeCoverage', response.data.data.stages[5].status);
        localStorage.setItem('codeInspection', response.data.data.stages[7].status);
        localStorage.setItem('E2ETest', response.data.data.stages[8].status);
        localStorage.setItem('performanceTest', response.data.data.stages[9].status);
        console.log(localStorage.getItem('activityValue'));
        this.$router.push('/activity'); 
      })
      .catch(function(err){
        console.log(err);
      });  
    },
    build() {
      this.jobName = localStorage.getItem('currentJob');
      this.buildNumber = localStorage.getItem('buildNumber');
      this.userName = localStorage.getItem('firstName');
      this.userId = localStorage.getItem('userId');
      this.jobId = localStorage.getItem('jobId');
      this.noOfDeploy = localStorage.getItem('numOfDep');
      // console.log(this.buildNumber);
      let buildData = {
        jobName : this.jobName,
        buildNumber :  this.buildNumber,
      };
      // console.log(buildData);
      axios.post(this.engineURL+"stageInfo", buildData)
      .then(response => {
        // console.log(response);
        if (response.status === 200)
        {
          //this.showSpinner = 0;
          var data = response.data.data[0];
          this.timings(data.durationMillis);
          // this.millis = data.durationMillis;
          this.startTimeMillis = data.startTimeMillis;
          this.endTimeMillis = data.endTimeMillis;
          this.duration = data.queueDurationMillis;
          this.stages = data.stages;
          this.buildStatus = data.status;
          localStorage.setItem('endTime', data.durationMillis);
          localStorage.setItem('buildStatus', this.buildStatus);
          // console.log("build start "+ this.millis);
          // console.log("build stage  "+ this.stages[0].name);
          if (data.status == "SUCCESS" || data.status == "FAILED")
          {
            // console.log("deploy done");
            clearInterval(this.interval);
            if (data.status == "SUCCESS"){
              this.status = '2';
            }
            else if (data.status == "FAILED"){
              this.status = '3';
            }
            else {
              this.status = ' ';
            }
            let updateData = {
              jobName : this.jobName,
              buildStatus :  this.status,
              userId : this.userId,
              jobId: this.jobId,
              buildNumber: this.buildNumber,
              buildEndTime: this.endTimeMillis,
              buildDuration:this.millis,
              buildStartTime: this.startTimeMillis,
            };
            this.buildInprogress = 1;
            this.updateBuild(updateData);
          }
        }
        else {
          this.buildInprogress = 1;
          this.notifyMsg();
        }
      });
  
      axios.post(this.engineURL+"buildStream", buildData)
      .then(response => {
        var resData = response.data;
        if (response.status === 200)
        {
          this.buildInprogress = 1;
          this.streamDisplay = 1;
          //console.log(response);
          this.streamData = resData.data;
        }
        else {          
          this.streamData = "<p> Something went wrong </p>";
        }
      })
      .catch(function(err){
        this.buildInprogress = 1;
        console.log("build stream "+err);
        this.streamDisplay = 0;
        this.streamData = "<p> Something went wrong </p>";
        // this.notifyMsg();
      }.bind(this));

    },
    // loadXML() {
    //   // let buildDep = {
    //   //   buildId : this.buildId,
    //   //   appName : this.$session.get('buildAppName'),
    //   //   authKey : this.$session.get('buildHerokuAuth'),
    //   // };
    //   this.info = "inprogress ";
    //   var rq = new XMLHttpRequest();
    //   rq.onreadystatechange = function(vm) {
    //     if (this.readyState === XMLHttpRequest.DONE) {
    //       console.log("hello");
    //       if (this.status === 200) {
    //         vm.info = this.responseText;
    //       } else {
    //         vm.info = "Request Failed";buildRunstatus
    //       }
    //     }
    //   }.bind(rq, this);
    //   rq.open("GET", this.url);
    //   setInterval(doStuff, 1000);
    //   rq.send();
    // }
    updateBuild(updateData) {
      clearInterval(this.interval);
      // console.log("after deploy "+updateData);
      axios.post(this.engineURL+"createBuild", updateData)
      .then(response => {
        if (response)
        {
          this.validated = 0;
          console.log("createBuild");
          // console.log(response.data.buildHistoryId);
          localStorage.setItem('buildHisId', response.data.buildHistoryId);
        }
      })
      .catch(function(err){
        console.log(err);
        this.validated = 0;
        // this.$router.push('/dashboard');
      }.bind(this));
      // this.$router.push('/dashboard');
    }
  },
  // computed: {
  //   streamUrl(){
  //     return (this.streamURL);
  //   }
  // },
  // mounted() {
  //  console.log("response.data");
  // },
};  
</script>
<style scoped>
.buildLoader {
  width: 2.0em;
}
pre {
  margin-top: 0;
  margin-bottom: 0rem;
  padding: 15px 15px 15px 25px !important;
  }
#loader-wrapper .loader-section {
position: fixed;
top: 0;
left: 0;
right: 0;
width: 100%;
height: 100%;
background: #22222236;
z-index: 1000;
}
.loaderGif{
width: 4.5% !important;
margin-left: 0.5em !important;
position: absolute !important;
bottom: 0.6em !important;
}
.spinnerLoader{
z-index: 100 !important;
position: absolute !important;
top: 35% !important;
left: 40% !important;
}
#getstartedHBtn{
    padding: 0.6em 0.8em !important;
    color: #ffffff !important;
    background-color: #8bc34aff !important;
    border-radius: 21px !important;
    text-transform: none !important;
    top: 1em !important;
    width: 11em !important;
    font-size: 17px !important;
}
img.imgprogress {
    height: 2% !important;
    width: 30px !important;
    /*
    position: relative;
    bottom: 3em;
    height: 10%;
    left: 9em;*/
}
.pendingStatus{
  color: #ff5723 !important;
}
.succesStatus{
   color: green !important; 
}
.failedStatus{
   color: red !important; 
}
.objectHeight{
  height: 500px !important;
}
 .failedTxt{
    color: #ff0000ff !important;
    font-size: 14px !important;
  }
  .overallCard{
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.39), 0 2px 10px 0 rgba(0, 0, 0, 0.3) !important;
  }
  .buildRunstatus{
    height: 510px !important;
    background-color: #343a4026 !important;
    overflow: auto !important;
  }
  .cardBuildcompl{
  box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 0.34), 0 2px 15px 0 rgba(0, 0, 0, 0.34) !important;
  border-left: 0.5em solid #8bc34aff !important;
  }
  .cardinprogress{
  box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 0.34), 0 2px 15px 0 rgba(0, 0, 0, 0.34) !important;
  border-left: 0.5em solid #f85729 !important;
  }
  .cardStarted {
    box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 0.34), 0 2px 15px 0 rgba(0, 0, 0, 0.34) !important;
    border-left:  0.5em solid #8bc34aff !important;
  }
  .cardFailed{
    box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 0.34), 0 2px 15px 0 rgba(0, 0, 0, 0.34) !important;
    border-left: 0.5em solid #ff0000ff !important;
  }
  .p-3 {
    padding: 0.23rem!important;
  }

 /*.cards{
  box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 0.34), 0 2px 15px 0 rgba(0, 0, 0, 0.34) !important;
  border-left:  1em solid #8bc34aff !important;
  }
 .cardsFailed{
  box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 0.34), 0 2px 15px 0 rgba(0, 0, 0, 0.34) !important;
  border-left: 1em solid #ff0000ff !important;
  }*/
@media only screen and (min-width: 300px) and (max-width: 576px){
    .contFluied{
      padding: 1rem !important;
      margin-top: 5rem !important;
    }
  }
</style>


