import Vue from 'vue';
import Router from 'vue-router';

import HomePage from '../docs/HomePage';
import SigninPage from '../docs/SigninPage';
import Signup from '../docs/Signup';
import pricingPage from '../docs/pricingPage';
import howItWorksPage from '../docs/howItWorksPage';
import addworkSpacePage from '../docs/addworkSpacePage';
import activityPage from '../docs/activityPage';
import buildLogPage from '../docs/buildLogPage';
import contactUs from '../docs/contactUs';
import CreateProjectPage from '../docs/CreateProjectPage';
import Dashboard from '../docs/Dashboard';
import profilePage from '../docs/profilePage';
import profilePage2 from '../docs/profilePage2';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: HomePage
    },
    {
      path: '/signin',
      name: 'SigninPage',
      component: SigninPage
    },
    {
      path: '/signup',
      name: 'Signup',
      component: Signup
    },
    {
      path: '/pricing',
      name: 'pricingPage',
      component: pricingPage
    },
    {
      path: '/howitworks',
      name: 'howItWorksPage',
      component: howItWorksPage
    },
    {
      path: '/workspace',
      name: 'addworkSpacePage',
      component: addworkSpacePage
    },
    {
      path: '/activity',
      name: 'activityPage',
      component: activityPage
    },
    {
      path: '/buildlog',
      name: 'buildLogPage',
      component: buildLogPage
    },
    {
      path: '/contact',
      name: 'contactUs',
      component: contactUs
    },
    {
      path: '/createproject',
      name: 'CreateProjectPage',
      component: CreateProjectPage
    },
    {
      path: '/dashboard',
      name: 'Dashboard',
      component: Dashboard
    },
    {
      path: '/profile',
      name: 'profilePage',
      component: profilePage
    },
    {
      path: '/profile2',
      name: 'profilePage2',
      component: profilePage2
    },
  ]
});



