        // console.log(response);
        this.$session.set('gitAccesstoken', response.data.accesstoken);
        this.$session.set('gitUserName', response.data.userName);
        this.gitRepository = response.data.repoName;
        var gitRepositoryURL=response.data.repoSourceUrl;
        var countURL = 0;
        this.gitRepository.forEach(element => {
          // console.log(element);
          this.options.push({value: gitRepositoryURL[countURL], text: element});
          countURL++;
        });
        // console.log(this.options);
