    jobNameValidation(){
      // this.steppercount=+1;  
      localStorage.setItem('steppercount', this.steppercount);
      var inpJobName=document.getElementById("inpJobName").value.trim(); 
      // console.log(inpJobName);
      localStorage.setItem('jobName', inpJobName);
      this.newName = localStorage.getItem('jobName');
      // this.jobname=localStorage.getItem('jobName');
      let jobData= {
        jobName : inpJobName,
      };
      axios.post(this.jobEngineURL+"checkJobName", jobData)
      .then(response => {
        // console.log(response);
        var resMessage = response.data.message;
        var resStatus = response.data.status;
        if (resStatus == 200 && resMessage == 'Success'){
          this.steppercount=+1;  
          localStorage.setItem('steppercount', this.steppercount);
          this.jobDisabled =1;
        }
      },
    err => {
      console.log(err.response);
      if (err.response.status == 401){
        this.errorMessage= 'Job Name already Exists!';  
        this.errorJobName = 1;  
        console.log(this.errorMessage) ;
      }
      else if (err.response.status == 400){
        this.errorMessage= 'Job Name is required';
        this.errorJobName = 1;
        console.log(this.errorMessage) ;
      }
      else {
        this.errorMessage= 'Something went wrong';
        this.errorJobName = 1;
        console.log(this.errorMessage) ;
      }
      this.jobDisabled =0;
    });
