      console.log(this.item.value);
      // this.spinnValidate = 1;
      if (this.item.text != '')
      {
        this.selBranch = 1;
        var accesstoken = this.$session.get('gitAccesstoken');
        var gitUserName = this.$session.get('gitUserName');
        let formData= {
          userName : gitUserName,
          repoName : this.item.text,
          authToken : accesstoken,
        };
        console.log(formData);
        axios.post("https://merida-engine.herokuapp.com/job/gitBranchList", formData)
        .then(response => {
          this.spinnValidate = 0;
          // console.log(response.data.status);
          console.log(response);
          var branchList=response.data.data;
          var branchIndex = 0;
          branchList.forEach(element => {
            console.log(element);
            this.options1.push({value: branchIndex, text: element.name});
            branchIndex++;
          });
          this.gitRepository = response.data.repoName;
        })
        .catch(function(err){
          this.spinnValidate = 0;
          console.log(err);
        });
      }
