          var branchList=response.data.data;
          var branchIndex = 0;
          // var a = ["1", "1", "2", "3", "3", "1"];
          var unique = branchList.filter(function(item, i, ar){ 
            return ar.indexOf(item) === i; 
          });
          console.log(unique);
          unique.forEach(element => {
            // console.log(element);
            this.options1.push({value: branchIndex, text: element.name});
            branchIndex++;
          });
          this.gitRepository = response.data.repoName;
        })
        .catch(function(err){
          this.spinnValidate = 0;
          console.log(err);
        });
      }
