<script>
import Vue from 'vue';
import Btn from '../components/Button';
import Column from '../components/Col';
import Row from '../components/Row';
import Container from '../components/Container';
import Card from '../components/Card';
import CardBody from '../components/CardBody';
import Dropdown from '../components/Dropdown';
import DropdownItem from '../components/DropdownItem';
import DropdownMenu from '../components/DropdownMenu';
import DropdownToggle from '../components/DropdownToggle';
import headerPage from '../docs/headerPage.vue';
import VueCircle from 'vue2-circle-progress';
import {mapState} from 'vuex';
import moment from 'moment';
Vue.use(require('vue-moment'));

export default {
  name: 'JustDeploy',
  computed: mapState([
    'userEngineURL',
    'engineURL',
    'jobEngineURL'
  ]),
  components: {
    Btn,
    Column,
    Row,
    Container,
    Card,
    CardBody,
    Dropdown,
    DropdownItem,
    DropdownMenu,
    DropdownToggle,
    headerPage,
    VueCircle
  },
  data(){
    return {
      projectname: localStorage.getItem('currentJob'),
      branchname: "Master",
      timetaken: '',
      lastupdate: '',
      version: "1.0.0",
      branchName: "Master",
      fill : { gradient: ["#ff5722ff"] },
      selecttestRepo: '',
      activityPercentage: '',
      gradePercentage: localStorage.getItem('activityPercentage'),
      gradeValue: localStorage.getItem('activityValue'),
      projectBuildStatus: localStorage.getItem('buildStatus'),
      unitTest: localStorage.getItem('unitTest'),
      codeCoverage: localStorage.getItem('codeCoverage'),
      codeInspection: localStorage.getItem('codeInspection'),
      E2ETest: localStorage.getItem('E2ETest'),
      performanceTest: localStorage.getItem('performanceTest'),
      e2eTest: "/static/E2EtestReport.html",
      mocha: "/static/mochawesome.html",
      lightHouse: "/static/meridareport.html",
      ccTest: "/static/index.html",
    };
  },
  methods:{
    timings(milliSecs) {
      // var current = Date.now();
      // var past = secs;
      var seconds = (milliSecs)/1000;
      seconds = Math.round(seconds);
      var d = Math.round(seconds);
      // console.log("ms: "+element.lastUpdated+" sec: "+seconds);
      if (seconds < 60){
        this.timetaken = seconds+" Seconds";
      }
      else if (seconds < 3600){
        var m = Math.floor(d % 3600 / 60);
        var mDisplay = m > 0 ? m + (m == 1 ? " minute " : " minutes") : "";
        this.timetaken = mDisplay;
      }
      else if (seconds < 86400){
        var h = Math.floor(d / 3600);
        // console.log(h);
        if (h < 24 && h > 0){
          var hDisplay = h > 0 ? h + (h == 1 ? " hour" : " hours") : "";
          this.timetaken = hDisplay;
        }
        else if (h >= 24){
          var DateDisplay1 = h > 0 ? h + (h == 1 ? " Day, " : " Days") : "";
          this.timetaken = DateDisplay1;
        }
      }
      else {
        var h1 = Math.floor(d / 86400);
        var DateDisplay = h1 > 0 ? h1 + (h1 == 1 ? " Day, " : " Days") : "";
        this.timetaken = DateDisplay;
      }
    },
    // progress(event,progress,stepValue){
    //   console.log(stepValue);
    // },
    // progress_end(event){
    //   console.log("Circle progress end");
    // }
  },
  mounted() {
    // console.log("hi this is local storage from home :"+localStorage.getItem('sonarUrl'));
    if (localStorage.getItem('sonarUrl') == 0){
      localStorage.setItem('sonarUrl', 2);
      location.reload(true);
      // console.log("hi this is local storage from home after if :"+localStorage.getItem('sonarUrl'));
    }
    this.timings(localStorage.getItem('timetaken'));
    var tempLastUpdate = localStorage.getItem('lastupdate');
    console.log(tempLastUpdate);
    this.lastupdate =moment.unix(tempLastUpdate/1000).format("MMMM DD YYYY hh:mm a");    
    // if (this.$session.get('loggedIn') != 1) {
    //   this.$router.push('/signin');
    // }
  }
};
