<template>
 <Ftr class="Footer">
      <p class="footer-copyright"><strong>
        &copy; Copyright:{{new Date().getFullYear()}}</strong></p>
    </Ftr>
</template>

<script>
import Ftr from '@/components/Footer.vue';
import Container from '../components/Container.vue';
import Row from '../components/Row.vue';
import Column from '../components/Col.vue';

export default {
  name: 'app',
  components: {
    Ftr,
    Container,
    Row,
    Column,
  }
};
</script>

<style scoped>
.Footer{
background: #f95623 !important;
height: 0px !important;

}
.footer-copyright{
	text-align: center !important;
	font-size: 16px !important;
  padding: 18px !important;
  background-color: #f95623 !important;
}
 p{
   margin-top: 0;
  margin-bottom: 0rem;
}
</style>



