           <button id="buildId" type="button" :disabled="validated == 1" class="btn deploy right" @click.once="deploy(asset._id, asset.userId, asset.jobName)">BUILD</button>
        </card>
  <!-- card for ready project -->   
          <card class="borderReady" v-if="asset.lastDeployStatus === '0'">
