<script>
import Container from '../components/Container.vue';
import Row from '../components/Row.vue';
import Column from '../components/Col.vue';
import Fa from '../components/Fa.vue';
import Navbar from '../components/Navbar.vue';
import NavbarItem from '../components/NavbarItem.vue';
import NavbarNav from '../components/NavbarNav.vue';
import NavbarCollapse from '../components/NavbarCollapse.vue';
import Btn from '../components/Button.vue';
import EdgHd from '../components/EdgeHeader.vue';
import StarRating from 'vue-star-rating';
import Card from '../components/Card.vue';
import CardImg from '../components/CardImg.vue';
import CardHeader from '../components/CardHeader.vue';
import CardBody from '../components/CardBody.vue';
import headerPage from '../docs/headerPage.vue';
import axios from 'axios';
import Vue from 'vue';
import {mapState} from 'vuex';
import Notify from 'vue2-notify';
Vue.use(Notify);
import VueSession from 'vue-session';
Vue.use(VueSession);
import VueSweetalert2 from 'vue-sweetalert2';
Vue.use(VueSweetalert2);
export default {
  name: 'HomePage',
  computed: mapState([
    'userEngineURL',
    'engineURL',
    'jobEngineURL'
  ]),
  components: {
    Container,
    Column,
    Row,
    Fa,
    Navbar,
    NavbarItem,
    NavbarNav,
    NavbarCollapse,
    Btn,  
    EdgHd,
    StarRating,
    Card,
    CardImg,
    CardHeader,
    CardBody,
    headerPage,
  },
  data(){
    return {
      source: "",
      repName:'',
      appId: '',
      siteName: "JustOps",
      text: '', 
      deploysuccess: '',
      showSpinner : 0,
      validated: 0,
      assets:[
        {
          status : 1,
        },
      ],
      length: '',
      pvalue: 0,
      // assets: [
      //   {
      //     status : 1,
      //   },
      // ]
    };
  },
  mounted(){
    // console.log("hi this is local storage from home :"+localStorage.getItem('sonarUrl'));
    if (localStorage.getItem('sonarUrl') == 0){
      localStorage.setItem('sonarUrl', 2);
      location.reload(true);
      // console.log("hi this is local storage from home after if :"+localStorage.getItem('sonarUrl'));
    }
    if (localStorage.getItem('loggedIn') == 1) {

      if (localStorage.getItem('deleted')) {
        localStorage.setItem('deleted',0);
        this.$notify.success('You successfully deleted this job');
      }
      var id= localStorage.getItem('userId');
      axios.get(this.userEngineURL+'job/'+id+'/list')
      .then(response => {
        if (response.data.status == 401) {
          this.pvalue= 1;
        }      
        var dat=response.data.data;
        this.length = dat.length;
        // console.log(dat);
        dat.forEach(element => {
          var current = Date.now(); 
          // console.log(current);
          var past = element.lastUpdated;
          // console.log(past);
          var seconds = (current- past)/1000;
          seconds = Math.round(seconds);
          // console.log(past);
          var d = Math.round(seconds);
          // console.log("ms: "+element.lastUpdated+" sec: "+seconds);
          if (seconds < 60){
            element.lastUpdated = seconds+" Seconds ago";
          }
          else if (seconds < 3600){
            var m = Math.floor(d % 3600 / 60);
            var mDisplay = m > 0 ? m + (m == 1 ? " minute ago " : " minutes ago") : "";
            element.lastUpdated = mDisplay;
          }
          else if (seconds < 86400){
            var h = Math.floor(d / 3600);
            // console.log(h);
            if (h < 24 && h > 0){
              var hDisplay = h > 0 ? h + (h == 1 ? " hour ago" : " hours ago ") : "";
              element.lastUpdated = hDisplay;
            }
            else if (h >= 24){
              var DateDisplay1 = h > 0 ? h + (h == 1 ? " Day ago, " : " Days ago") : "";
              element.lastUpdated = DateDisplay1;
            }
          }
          else {
            var h1 = Math.floor(d / 86400);
            var DateDisplay = h1 > 0 ? h1 + (h1 == 1 ? " Day ago, " : " Days ago") : "";
            element.lastUpdated = DateDisplay;
          }

          if (!element.lastDeployStatus == 1){
            this.assets.push(element);
          }
          else if (element.lastDeployStatus == 0){
            this.assets.push(element);
          }
          else if (element.lastDeployStatus == 2){
            this.assets.push(element);
          }
          else if (element.lastDeployStatus == 3){
            this.assets.push(element);
          }
          // console.log("asset "+ this.assets);
        });
      });
    }
    else {
      // this.$router.push('/signin');
    }
  },
  methods: {
    notifyMsg() {
      this.$notify.error('Please Try Again!!');
    },
    repoDelete(id) {
      if (this.validated == 1) {
        // console.log("hai");
      }
      else {
        var jobId = id;
        this.$swal({
          title: 'Are you sure?',
          text: 'You can\'t revert your action',
          type: 'warning',
          showCancelButton: true,
          confirmButtonText: 'Yes Delete it!',
          cancelButtonText: 'No, Keep it!',
          showCloseButton: true,
          showLoaderOnConfirm: true
        })
        .then(result => {
          if (result.value) {
            axios.get(this.jobEngineURL+jobId+'/delete')
            .then(response => {
              if (response.status === 200){
                localStorage.setItem('deleted', 1);
                window.location.reload();
              }
              else {
                this.$swal('Failure', 'Something went wrong', 'info');
              }
            })
            .catch(function(err){
              if (err){
                console.log(err);
                this.$swal('Failure', 'Something went wrong', 'info');
              }
            }.bind(this));
          } 
        });
      }
    },
    deploy(jobId, userId, jobName, numOfDep) {
      // console.log(userId);
      this.validated = 1;
      this.showSpinner = 1;
      let buildData = {
        jobName
      };
      axios.post(this.engineURL+"deployJob", buildData)
      .then(response => {
        // console.log(response);
        if (response.status === 200) {

          var resData=response.data.data;
          localStorage.setItem('currentJob', jobName);
          localStorage.setItem('buildNumber',resData[0].buildNumber);
          localStorage.setItem('jobId', jobId);
          localStorage.setItem('numOfDep', numOfDep);
          this.showSpinner = 0;
          this.$router.push('/buildLog');
        }
        else {
          this.notifyMsg();
        }
      })
      .catch(function(err){
        console.log(err);
        this.showSpinner = 0;
        this.validated = 0;
        this.notifyMsg();
      }.bind(this));
    },
  }
};
</script>
