<script>
import Btn from '../components/Button';
import Column from '../components/Col';
import Row from '../components/Row';
import Container from '../components/Container';
import Card from '../components/Card';
import CardBody from '../components/CardBody';
import headerPage from '../docs/headerPage.vue';
import firebase from 'firebase';
import {mapState} from 'vuex';
import axios from 'axios';
// var chargebee = require('chargebee');
export default {
  name: 'JustDeploy',
  computed: mapState([
    'userEngineURL',
    'engineURL',
    'jobEngineURL'
  ]),
  components: {
    Btn,
    Column,
    Row,
    Container,
    Card,
    CardBody,
    headerPage,
  },
  data(){
    return {
      loggedIn: localStorage.getItem('loggedIn'),
      priceLog: 0,
    };
  },
  beforemount() {
    this.loggedIn = localStorage.getItem('loggedIn') || '0';
  },
  mounted() {
    // console.log(localStorage.getItem('sonarUrl'));
    if (localStorage.getItem('sonarUrl') == 0){
      localStorage.setItem('sonarUrl', 2);
      location.reload(true);
    }
    if (localStorage.getItem('pricing') == 'isTrue'){
      // localStorage.setItem('pricing', 'isFalse');
      window.location.reload(true);
    }
    localStorage.setItem('pricing', 'isFalse');
    this.priceLog = localStorage.getItem('loggedIn');
    
  },
  methods: {
    pricingStart(){
      var sessionSet = localStorage.getItem('loggedIn');
      if (sessionSet == 1) {
        this.$router.push('/dashboard');
      }
      else {
        this.googlesignup();
      }

    },
    errorNotification(){
      this.$notify.error('Please Try Again Later!!!');
    },
    googlesignup() {
      this.isShow = 1;
      var baseProvider = new firebase.auth.GoogleAuthProvider();
      firebase.auth().signInWithPopup(baseProvider)
        .then(function(result){
          // console.log(result);
          // console.log(result.user.email);
          var fields = result.user.displayName.split(' ');
          let newUser = {
            "firstName": fields[0],
            "lastName": fields[1],
            "email": result.user.email, 
            "profileImage": result.user.photoURL,
            "firebaseId": result.user.uid, 
          };
          // console.log(newUser);
          this.signup(newUser);
        }.bind(this))
        .catch(function(err){
          if (err){
            this.errorNotification();
          }
        });
    },
    signup(newUser) {
      axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
      axios.post(this.userEngineURL+"google_login", newUser)
        .then(function(response){
          if (response.status === 200) {
            // console.log(response.data);
            var res = response.data;

            localStorage.setItem('firstName', res.data.firstName);
            localStorage.setItem('lastname', res.data.lastname);
            localStorage.setItem('email', res.data.email);
            localStorage.setItem('userId', res.data._id);
            localStorage.setItem('profileImage', res.data.profileImage);
            localStorage.setItem('loggedIn', '1');
            // console.log(this.$session.get('userName'));
            // console.log(this.$session.get('lastname'));
            // console.log(this.$session.get('email'));
            //this.$session.get('userId',res.data._id);
            // this.$swal('User', 'Successfully LoggedIn');
            if (response.data.message == "Logged In Successfully") {
              this.$notify.success('LoggedIn Successfully!!');  
            }
            if (response.data.message == "User created") {
              this.$notify.success('SignedUp Successfully!!');  
            }
            this.$router.push('/dashboard');
          }
          else {
            this.$router.push('/');
          }
        }.bind(this))
        .catch(function(err){
          if (err){
            this.errorNotification();
          }
        }.bind(this));
    },
  },
  // mounted() {
  //   if (this.$session.get('loggedIn') != 1) {
  //     this.$router.push('/signin');
  //   }
  // },
  // mounted() {
  //   let charge = chargebee.getInstance();
  //   console.log(charge);
  //   let chargebeeInstance = chargebee.init({
  //     site: "cogzidel-test"
  //   });
  //   chargebeeInstance.openCheckout({
  //     hostedPage(result) {
  //       let id = {
  //         customer_id: this.customer_id,
  //         plan_id: this.plan_id,
  //         subscriptionId: result.subscription.id,
  //         subscriptionStatus: result.subscription.status,
  //         planId: result.subscription.plan_id,
  //         planUnitPrice: result.subscription.plan_unit_price,
  //         nextBillingAt: result.subscription.next_billing_at
  //       };
  //       axios.post("/charge/callback",id)
  //       .then(response => {
  //         console.log("hello");
  //         console.log(response);
  //       });
  //     },
  //  success callback
  // success(hostedPageId) {
  //   console.log("fhsdjk");
  // }
