<template>
  <div id="app">
    <!-- <nav :class="className" :is="tag" :style="navStyle">
     <a :href="href" class="navbar-brand" v-if="header2 == 1">{{name}}
      <img src="../assets/justopswhite.png" style="width: 15%;">
    </a>
    </nav> -->
    <navbar dark position="top" class="default-colors" scrolling>
      <navbar-collapse>
      <navbar-nav left>
      <a :href="href" class="navbar-brand" v-if="header2 == 1">{{name}}
      <img src="../assets/justopswhite.png" style="width: 15%;">
      </a>
    </navbar-nav>
        <navbar-nav right>
          <navbar-item waves-fixed><a class="navbarItem1" href="#/howitworks">How it Works</a></navbar-item>
          <navbar-item waves-fixed><a class="navbarItem1" href="#/pricing">Pricing</a></navbar-item>
          <div>
          <navbar-item waves-fixed v-if="loggedIn === '0'"><a class="navbarItem1" href="#/signin" v-if="loggedIn === '0'">Log In</a></navbar-item>
        </div>
          <div>
          <navbar-item waves-fixed v-if="loggedIn === '0'"><a class="navbarItem1" href="#/signup" v-if="loggedIn === '0'">Sign Up</a></navbar-item>
           <navbar-item waves-fixed v-b-tooltip.hover title="Sign out" v-else> <a href="#" v-on:click="logout" class="link naveUser1"><span><b>{{ userName   }}</b></span></a></navbar-item>
         </div>
        </navbar-nav>
      </navbar-collapse>
    </navbar>
 <!--    <navbar dark position="top" class="default-color1"  href="#/" scrolling v-show="header2">
      <navbar-collapse>
        <navbar-nav right>
          <navbar-item waves-fixed><a class="navbarItem2" href="#/howitworks">How it Works</a></navbar-item>
          <navbar-item waves-fixed><a class="navbarItem2" href="#/pricing">Pricing</a></navbar-item>
          <navbar-item waves-fixed><a class="navbarItem2" href="#/signin">Log In</a></navbar-item>
          <navbar-item waves-fixed><a class="navbarItem2" href="#/signup">Sign Up</a></navbar-item>
        </navbar-nav>
      </navbar-collapse>
    </navbar> -->
    <main :style="{marginTop: '0px'}">
     <!--  <router-view></router-view> -->
    </main>
   <!--  <Ftr color="default-color">
      <p class="footer-copyright mb-0 py-3 text-center">
        &copy; {{new Date().getFullYear()}} Copyright: <a href="https://www.MDBootstrap.com"> MDBootstrap.com </a>
      </p>
    </Ftr> -->
  </div>
</template>

<script>
import Navbar from '@/components/Navbar.vue';
import NavbarItem from '@/components/NavbarItem.vue';
import NavbarNav from '@/components/NavbarNav.vue';
import NavbarCollapse from '@/components/NavbarCollapse.vue';
import Ftr from '@/components/Footer.vue';
import EdgHd from '@/components/EdgeHeader.vue';
import Vue from 'vue';
import * as VueGoogleMaps from 'vue2-google-maps';
Vue.use(VueGoogleMaps, {
  load: {
    key: 'AIzaSyAdGNDI0COpfc1Hz009rqioZdklYtsm55E',
    libraries: 'places',
    // libraries: 'places', //// If you need to use place input
  }
});
export default {
  name: 'app',
  components: {
    Navbar,
    NavbarItem,
    NavbarNav,
    NavbarCollapse,
    Ftr,
    EdgHd,
  },
  data() {
    return { 
      loggedIn: localStorage.getItem('loggedIn') || '0',
      userName:"",
    };
  },
  methods: {
    logout() {
      localStorage.clear();
      window.location.reload(true);
      // this.$notify.success('Logout Successfully!!');      
      this.$router.push('/');
    },
  },
  beforeMount() {
    this.userName = localStorage.getItem('firstName') || '';
    this.loggedIn = localStorage.getItem('loggedIn') || '0';
  },
};

</script>

<style>
.flyout {
  display:flex;
  flex-direction: column;
  /*min-height:100vh;*/
  justify-content: space-between;
}
/*.default-color{
  background-color: #FFFFFF !important;
}
.navbarItem1{
  color: #000000 !important;
}*/
.default-colors{
  background-color: #f95523 !important;
}
.navbarItem1{
  color: #FFFFFF !important;
  font-weight: normal !important;
}
.fixed-top{
  position: unset !important;
}
a .naveUser1{
  color: #FFFFFF !important;
}
</style>



