<template>
  <div>
  <headerPage :key="$route.fullPath"></headerPage>
  <div class="container-fluid overallContF">
    <row>
      <column class="mt-5" xl="6" lg="12" md="12" sm="12 cardCol1">
        <card class="contactCard p-5">
          <h4 class="font-weight-bold">GET IN TOUCH</h4>
          <row>
            <column class="p-3" md="6" sm="6">
            <label class="lablesText">TELL US YOUR NAME</label>
            <input type="text" name="" id="inpName" class="inputText">
          </column>
          <column class="p-3" md="6" sm="6">
            <label class="lablesText">ENTER YOUR EMAIL</label>
            <input type="email" name="" id="inpEmail" class="inputText">
          </column>
          </row>
          <row class="p-3">
            <label class="w-100 lablesText" >SUBJECT</label>
            <input class="w-100 inputText" id="inpSubject" type="text" name="">
          </row>
          <div class="form-group pt-3 pb-3 lablesText">
           <label for="exampleFormControlTextarea2">MESSAGE</label>
         <textarea class="form-control rounded-0 textArea" id="exampleFormControlTextarea2" rows="3"></textarea>
         </div>
         <button type="button" id="btnSendEmail" class="btn btn-primary subBtn">Submit</button>
        </card>
      </column>
      <column class="mt-5 cardCol2" xl="6" lg="12" md="12" sm="12">
        <div class="m-auto contactInfo">
        <p class="contactText font-weight-bold">CONTACT INFO</p>
        <p class="mt-4 addrText ">184, North velli street, Madurai Main, Madurai<br/>Tamil Nadu 625001</p>
        <p class="mt-3 supportDetail">+91 -452-234 4002 <br/> support@cogzidel.com</p>
        <div>
          <!-- images -->
        </div>
    </div>
      </column>
  </row>
  </div>
</div>
</template>
<script >
import Btn from '../components/Button';
import Column from '../components/Col';
import Row from '../components/Row';
import Container from '../components/Container';
import Card from '../components/Card';
import CardBody from '../components/CardBody';
import MdInput from '../components/MdInput.vue';
import MdTextarea from '../components/MdTextarea.vue';
import headerPage from '../docs/headerPage.vue';
  
export default {
  name: 'JustDeploy',
  components: {
    Btn,
    Column,
    Row,
    Container,
    Card,
    CardBody,
    'mdinput': MdInput,
    'mdtextarea': MdTextarea,
    headerPage,
  },
  mounted(){
    console.log("hi this is local storage from home :"+localStorage.getItem('sonarUrl'));
    if (localStorage.getItem('sonarUrl') == 0){
      localStorage.setItem('sonarUrl', 2);
      location.reload(true);
      console.log("hi this is local storage from home after if :"+localStorage.getItem('sonarUrl'));
    }
  },
};
</script>
<style scoped>
.overallContF{
  background-color: #ff5722ff !important;
  width: 100% !important;
  height: 53em !important;
}
.contactCard{
  border-radius: 5px !important;
  width: 85% !important;
  height: 100% !important;
  margin: auto !important;
}
.inputText {
  width: 14em;
}
.subBtn{
  width: 40% !important;
  border-radius: 2px !important;
  margin: auto !important;
  background-color: #8bc34aff !important;
}
.lablesText{
  font-size: 14px !important;
}
.contactText{
  font-size: 24px !important;
  color: #ffffff !important;
}
.addrText{
  font-size: 20px !important;
  color: #ffffff !important;
  font-weight: 400 ;
}
.supportDetail{
  font-size: 20px !important;
  color: #ffffff !important;
  font-weight: 400 ;
}
.inputText{
  background-color: #00000026 !important;
  height: 2em !important;
  border: none !important;
  border-radius: 2px !important;
}
.textArea{
  background-color: #00000026 !important;
  border: none !important;
  border-radius: 2px !important;
}
.contactInfo{
  margin-top: 10em !important;
  margin-left: 5em !important;
}
@media (min-width: 768px) and (max-width: 991.98px){

.overallContF{
height: 1000px !important;
}
.cardCol1{
  width: 100% !important;
}
.cardCol2{
  width: 100% !important;
}
.contactInfo{
  margin-top: 2em !important;
  margin-left: 8em !important;
}
}
@media (min-width: 992px) and (max-width: 1199.98px){
.overallContF{
    height: 84em !important;
}
.cardCol1{
  width: 100% !important;
}
.cardCol2{
  width: 100% !important;
}
.contactInfo{
  margin-top: 2em !important;
  margin-left: 14em !important;
}
.contactCard{
  width: 55% !important;
}
}
</style>


