<template>
  <div>
  <headerPage :key="$route.fullPath"></headerPage>
<div class="overallDiv">
<row class="signinRowone ml-0 mr-0">
  
</row>  
<row class="signinRowtwo ml-0 mr-0">
  <card class="cardSigin text-center">
    <img src="../assets/justopslogo.svg" class="logoImg">
    <p class="font-weight-bold" style="font-size: 20px;">Sign In to your Workspace</p>
    <div>
   <button type="button" v-on:click="googlesignin"  id="googleSignInn" class="googleButton btn btn-google-plus"><i class="fa fa-google-plus pr-1"></i>  Sign In With Google</button>
 </div>
 <!-- <img class="Glogo" src="../assets/GplusLogo.png"/> <img src="../assets/line.png">-->
  <p>Don't have an account yet?<a href="#/signup"><span class="signupHov ml-2 d-inline-block" style="color: rgb(139, 195, 74);  font-weight: bold;"> Sign Up</span></a></p>
  <!-- <Spinner v-show="isShow" name="ball-clip-rotate-multiple" color="#ff5722ff" /> -->
  <Spinner class="spinner" v-show="isShow" name="ball-clip-rotate-multiple" color="#000000"/>

  </card>
</row>
</div>
 </div>
</template>
<script src="https://www.gstatic.com/firebasejs/5.0.4/firebase.js"></script>
<script>
import Btn from '../components/Button';
import Column from '../components/Col';
import Row from '../components/Row';
import Container from '../components/Container';
import Card from '../components/Card';
import CardBody from '../components/CardBody';
import firebase from 'firebase';
import axios from 'axios';
import Vue from 'vue';
import headerPage from '../docs/headerPage.vue';
import VueSession from 'vue-session';
import {mapState} from 'vuex';
Vue.use(VueSession);
import Notify from 'vue2-notify';
Vue.use(Notify);
import Spinner from 'vue-spinkit';
Vue.component('Spinner', Spinner);
var config = {
  apiKey: "AIzaSyBhp8Dv_RQaXvsBMEdh9waL9NIFxe3ZbzI",
  authDomain: "justops-merida.firebaseapp.com",
  databaseURL: "https://justops-merida.firebaseio.com",
  projectId: "justops-merida",
  storageBucket: "justops-merida.appspot.com",
  messagingSenderId: "361341414152"
};
firebase.initializeApp(config);
export default {
  name: 'JustDeploy',
  computed: mapState([
    'userEngineURL'
  ]),
  components: {
    Btn,
    Column,
    Row,
    Container,
    Card,
    CardBody,
    headerPage,    
  },
  data(){
    return {
      userName: '',
      email: '',
      phonenumber: '',
      photo: '',
      uid: '',
      // test:"test123",
      text: '',
      isShow: 0,  
    };
  },
  // mounted(){
  //   if (localStorage.getItem('sonarUrl') == 0){
  //     localStorage.setItem('sonarUrl', 2);
  //     location.reload(true);
  //   }
  // },
  methods: {
    errorpopup(){
      this.isShow = 0;
      this.$notify.error('Please Try Again Later!!!');
    },
    googlesignin() {
      console.log("df slkjsbdfghjdsvbkgb.sljfgkb");
      var baseProvider = new firebase.auth.GoogleAuthProvider();
      // console.log(baseProvider);
      firebase.auth().signInWithPopup(baseProvider)
      .then(function(result){
        this.isShow = 1;
        if (baseProvider){
          // console.log(result);
          //console.log(result.user.providerData[0].email);
          var fields = result.user.displayName.split(' ');
          let oldUser = {
            "firstName": fields[0],
            "lastName": fields[1],
            "email": result.user.email, 
            "profileImage": result.user.photoURL,
            "firebaseId": result.user.uid, 
          };
          // console.log(oldUser);
          this.login(oldUser);
        }
      }.bind(this))
      .catch(function(err){
        if (err){
          // this.isShow = 0;
          console.log("error");
          this.errorpopup();
        }
      });
    },
    login(oldUser) {
      // console.log("hfjdhjk");
      axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';
      axios.post("https://merida-engine.herokuapp.com/users/google_login", oldUser)
        .then(function(response){
          this.isShow = 1;
          if (response.status === 200) {
            // console.log(response);
            var res = response.data;
            localStorage.setItem('firstName', res.data.firstName);
            localStorage.setItem('lastname', res.data.lastname);
            localStorage.setItem('email', res.data.email);
            localStorage.setItem('userId', res.data._id);
            localStorage.setItem('profileImage', res.data.profileImage);
            localStorage.setItem('loggedIn', '1');
            //console.log(localStorage.getItem('testlocal'));
            // console.log(res.message);
            // console.log(res.data.email);    
            // this.$session.set(res.data);
            // console.log(this.$session.get('userName'));
            // console.log(this.$session.get('lastname'));
            // console.log(this.$session.get('email'));
            
            // this.isModalVisiblesigups=false;
            // this.isModalVisiblelog=false;
            // console.log(this.$session.get('firstName'));
            if (response.data.message == "Logged In Successfully") {
              this.$notify.success('LoggedIn Successfully!!');  
            }
            if (response.data.message == "User created") {
              this.$notify.success('SignedUp Successfully!!');  
            }
            this.$router.push('/dashboard');
            // this.isShow = 0;
          }
          else {
            this.isShow = 0;
            this.$router.push('/');
          }

        }.bind(this))
        .catch(function(err){
          // this.isShow = 0;
          if (err){
            this.errorpopup();
            console.log(this.errorpopup());
            console.log("errorpopup");
          }
        
        }.bind(this));
    }
  }
};
</script>
<style scoped>
.spinner{
  margin: 2em 0em 0em 15em !important;
  width: 10em;
  height: 5em auto ;
}
.overallDiv{
    /*margin-top: 4em;*/
    background-color: #ff5722ff;
}
/*.Glogo{
  height: 15px;
  position: absolute;
  left: 8.3em !important;
  margin-top: 0.4em;
}
.goog*/leButtons{
    margin-top: 3em;
    background: #ca3523 !important;
    margin-top: 1.3em !important;
    text-transform: none !important;
    width: 10% !important;
    height: 12% !important;
    font-size: 17px !important;
}
.signinRowone{
  height: 17em !important;
}
.signinRowtwo{
  min-height: 48.5em !important;
  background-image: url("../assets/Just Deploy_Sign in background.svg");
}
.cardSigin{
    width: 35% !important;
    margin: -12em auto 0em auto !important;
    height: 450px !important;
    border-radius: 4px !important;
    z-index: 10 !important;
  }
  .logoImg{
    margin: 4em auto 2em auto !important;
    width: 12% !important;
  }
  .googleButton{
    display: inline-block;
    margin: 2.5em auto 1em 0.02em !important;
    /*margin: 2em auto 1em auto !important;*/
    background: #ca3523 !important;
    /*border-radius: 3px !important;*/
    text-transform: none !important;
    /*width: auto !important;*/
    height: auto !important;
    font-size: 17px !important;
  }
  .signupHov::after {
    content: '';
    display: block;
    width: 0px;
    height: 2px;
    background: #8bc34aff;
    transition: width .3s;
  }
   .signupHov:hover::after {
    width: 100%;
  }
 @media only screen and (min-width: 300px) and (max-width: 639.98px)
 {
 .cardSigin{
   width: 100% !important;
   height: auto !important;
   box-shadow: none !important;
 }
 .signinRowone{
  height: 14em !important;
  }
 .overallDiv{
   background-color: #ffffff !important;
   height: auto !important;
   margin-top: 0px !important;
}
.signinRowtwo{
  height: auto !important;
}

 }
 @media only screen and (min-width: 640px) and (max-width: 800px){
 .cardSigin{
   width: 65% !important;
   height: 450px !important;
   margin: -12em auto 0em auto !important;
 }
  .signinRowtwo{
    height: 45em !important;
  }
.overallDiv{
margin-top: -0.1em !important;
}
.spinner{
  margin: 2em 0em 0em 16em !important;
}
}
/*@media (min-width: 768px) and (max-width: 991px) {
.signinRowtwo{
  height: 48.5em !important;
}
}*/
 @media (min-width: 992px) and (max-width: 1399px)
 {
  .signinRowtwo{
    height: 64em !important;
    }
  .cardSigin {
     margin: -11em auto 0em auto !important;
 }
 .overallDiv {
    margin-top: -0.1em !important;
  }
  .spinner{
  margin: 2em 0em 0em 16em !important;
}
  }
</style>


